(function () {
    'use strict';
    var angular = require('angular');

    var officeCityMismatchComp = angular.module('app').component('officeCityMismatch', {
        template: require('./office-city-mismatch.component.html'),
        controller: ['$anchorScroll', 'analyticService', 'appState', function ($anchorScroll, analyticService, appState) {
            var vm = this;
            vm.$onInit = function () {
                vm.isStaffJourney = appState.isStaffJourney();
                analyticService.trackPageLoad(analyticService.pageList.officeCityMismatch);
                appState.clearAll();
                $anchorScroll();
            };
            vm.Close = function () {
                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = officeCityMismatchComp.name;
})();


